body {
  padding: 0;
  margin: 0;
  font-family: $lato;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  min-height: 100%;
  color: $color-black;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 p {
  margin: 0;
}

p {
  margin: 0;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
